export default {
	props: {
		// 传入的值
		count: {
			default: 0
		}
	},
	data() {
		return {
			// 显示的值
			value: 0,
		}
	},
	watch: {
		// 传入的值发生改变
		count(val,old) {
			val = parseInt(val.replace(/,/g, ''))
			let step = Math.floor((val - this.value) / 30)
			clearInterval(this.timeId)
			this.timeId = setInterval(() => {
				if (step >= val - this.value) {
					clearInterval(this.timeId)
					this.value = val
				} else {
					this.value += ++step
				}
			}, 30)

		}
	}
}
